DataManager.setCombined("ub.actions", {

  setup: function (ctx, tb) {
    let item = Object.create(this);
    item.ctx = ctx;
    item.state = {
      nextId: 1, skiped: {}
    }
    item.tb = tb;

    return item;
  },
  loadItem: function (op) {
    let master = this;
    this.tb.db.loadItems([op.id]).then(item => {
      if (op.pin)
        master.putValue(op.pin, item[0])
    })
  },

  loadItems: function (op) {
    let master = this;
    this.tb.db.loadItems(op.ids).then(item => {
      if (op.pin)
        master.putValue(op.pin, item)
    })
  },

  searchText: function (op) {
    let master = this;
    this.tb.db.searchText(op.text).then(item => {
      if (op.pin)
        master.putValue(op.pin, item)
    })
  },

  runTriggers: function (triggers, value, isPartial) {
    triggers(value, isPartial)
  },

  putValue: function (path, value, isPartial) {
    _.set(this.ctx.data, path, value);

    if (this.ctx.triggers[path]) {
      this.runTriggers(this.ctx.triggers[path], value, isPartial)
    }

    if (isPartial) return;

    if (this.state.running) {
      this.state.shouldRerun = true;
    }
    else {
      this.runAllOps();
    }
  },

  prepareOp: function (op) {
    let args = {};
    for (var i in op.input) {
      if (i == "type") args.type = op.input[i];
      else if (!_.isString(op.input[i])) args[i] = op.input[i]
      else {
        let value = _.get(this.ctx.data, op.input[i]);
        if (value === undefined) {
          if (!op.partial)
            return null;
          else {
            if (!args.missing) args.missing = [];
            args.missing.push(i);
          }
        }
        else args[i] = value;
      }
    }
    for (var i in op) {
      if (i != "input")
        args[i] = op[i]
    }

    return args;
  },
  runOp: function (op) {
    if (!op.uid) op.uid = "op_" + this.state.nextId++;
    let args = this.prepareOp(op);
    if (args === null) {
      return "notcomplete";
    }

    let value = this[args.type](args, op);

    if (value === "notcomplete") return "notcomplete";
  },

  runAllOps: function () {
    let ncOps = [];

    this.state.running = true;
    this.state.shouldRerun = false;


    for (var i in this.ctx.ops) {
      let op = this.ctx.ops[i];
      let value = this.runOp(op);
      if (value === "notcomplete") ncOps.push(op)
      else this.state.shouldRerun = true;
    }

    this.ctx.ops = ncOps;

    this.state.running = false;

    if (this.state.shouldRerun) {
      this.state.shouldRerun = false;
      this.runAllOps()
    }

  },

  fn: function (op) {
    let result = op.fn(op, this);

    if (result === "notcomplete") return result;

    if (op.pin) {
      this.putValue(op.pin, result)
    }
  },

  assembleObj: function (args, op) {
    let obj = {};

    for (var i in op.input) {
      obj[i] = args[i]
    }

    if (op.partial && args.pin && args.missing) {

      if (!this.state.skiped[op.uid]) this.state.skiped[op.uid] = { missing: [] }


      obj.partial = true;

      if (_.isEqual(args.missing, this.state.skiped[op.uid].missing)) {
        return "notcomplete";
      }
      else
        this.state.skiped[op.uid].missing = args.missing;



      this.putValue(args.pin, obj, args.missing ? "skipRerun" : false)
    }

    else if (args.pin && !args.missing)
      this.putValue(args.pin, obj)

    if (args.missing) return "notcomplete";

  },

  listTemplate: function (v, master) {
    let list = [];

    for (var i in v.list) {
      let item = cu.moldAssembler.run(v.tpl, v.list[i]);
      list.push(item)
    }


    if (v.pin) {
      this.putValue(v.pin, list)
    }

  },

  useTpl : function (v, master) {
    let list = [];

    let item = cu.moldAssembler.run(v.tpl, v);


    if (v.pin) {
      this.putValue(v.pin, item)
    }


    return item;
  },

})

