DataManager.setCombined("ub.loaders.hud", {
  setup: function (opts) {
    _.merge(this, opts)
  },

  load: function () {
    let repo = {};

    let tpls = {
      isCollection: true,
      search: {
        highlight: "{highlighting.text}",
        id: "{doc.id}",
        mapId: "{doc.mapId}",
        started: "{doc.startedAt}",
        type: "{doc.type}",
        score: "{score}"
      }
    }

    let molds = cu.tplObjParse.run(tpls);

    let data = {
      antId: "ant_10",

      makeStats: function (v, master) {
        let stats = {};

        let subs = v.map.rels.sub;
        let currentId = v.root.id;

        let count = 0;

        count += Object.keys(subs[currentId] || {}).length;

        return { directSubs: count, created: v.root.placedAt }
      },

      tpls: molds,

    }

    let ctx = {};
    ctx.data = data;
    ctx.triggers = this.triggers;

    ctx.ops = [
      { type: "loadItems", input: { ids: "infoIds" }, pin: "infos" },
      { type: "loadItem", input: { id: "antId" }, pin: "root" },
      { type: "loadItem", input: { id: "root.mapId" }, pin: "parentMap" },
      { type: "loadItem", input: { id: "statsId" }, pin: "stats" },
      {
        type: "fn", input: { info: "parentMap.rels.info", antId: "antId" },
        fn: op => Object.keys(op.info[op.antId] || {}), pin: "infoIds"
      },
      { type: "fn", input: { root: "root", map: "parentMap", fn: "makeStats" }, pin: "stats" },
      { type: "searchText", input: { text: "root.text" }, pin: "searchResultsRaw" },
      { pin: "outcome", partial: true, type: "assembleObj", input: { root: "root", stats: "stats", "matches": "searchResults" } },
      { pin: "searchResults", input: { tpl: "tpls.search", list: "searchResultsRaw.rows" }, type: "listTemplate" }
    ]
    let actions = this.tb.actions.setup(ctx, this.tb)

    actions.runAllOps();
  },
})


