DataManager.setCombined("tb.persisitence", {
  setup: function () {
    this.db = cu.db.run("thisdb");
    window.onbeforeunload = () => { return this.saveAll() };
    this.loadAll();

  },

  request_save: function () {
    if (this.timer) window.clearTimeout(this.timer);

    this.timer = window.setTimeout(() => {
      this.saveAll()
    }, 60000)
  },

  saveAll: function () {
    let tosave = DataManager.getValue("dbSavings").items
    let savings = []
    for (var i in tosave) {
      savings.push(this.saveitem(i, tosave[i]))
    }

  },
  saveitem: function (id, target) {
    let path = target.path || target;
    let repo = DataManager.getWallet(path);
    return this.db.save({ repo }, id)
  },

  loadAll: function () {
    let tosave = DataManager.getValue("dbSavings").items;

    for (var i in tosave)
      this.loaditem(i, tosave[i]);
  },

  loaditem: function (id, target) {
    let path = target.path || target;
    let onload = target.onload || null;
    this.db.load(id).then((items) => {
      if (items && items.repo) DataManager.set(path, items.repo)
      if (onload) DataManager.runOn(onload[0], onload[1])
    })
  },

  starter: {
    loader: {
      autostart: true,
      get: (tb) => {
        if (tb.cache.done) return 1;
        tb.cache.done = true;
        let v = tb.fetch({ tb: "tb.persisitence" });
        v.tb.setup();

        return 1;
      }
    }
  }

})




