class Frame extends Component {
  constructor(props) {
    super(props);
    this.state = {}
  }

  path = "antsFrame.ui"

  componentDidMount() {
    DataManager.setTrigger(this.path, "asd", {
      run: () => {
        this.setState({ "version": new Date().getTime() })
      }
    })
  }

  componentDidUpdate() {
    myUB.afterRenderActions.runAll();
  }

  render() {
    let components = DataManager.getValue(this.path);
    let node = cu.reactUtils.makeComponent(components || []) || "empty";
    return node
  }
}



